@font-face {
    font-display: fallback;
    font-family: "OpenSans";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff")
}

@font-face {
    font-display: fallback;
    font-family: "OpenSans";
    font-style: italic;
    font-weight: 400;
    src: url("../fonts/OpenSans-Italic.woff2") format("woff2"), url("../fonts/OpenSans-Italic.woff") format("woff")
}

@font-face {
    font-display: fallback;
    font-family: "OpenSans";
    font-style: normal;
    font-weight: 600;
    src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff")
}

@font-face {
    font-display: fallback;
    font-family: "OpenSans";
    font-style: italic;
    font-weight: 600;
    src: url("../fonts/OpenSans-SemiBoldItalic.woff2") format("woff2"), url("../fonts/OpenSans-SemiBoldItalic.woff") format("woff")
}

body {
    font-size: 1em;
    font-size: calc( 14px + ( 16 - 14) * ( ( 100vw - 300px) / ( 1400 - 300)));
    height: 100%;
    line-height: 1.5;
}

body {
    font-family: Arial, Helvetica, sans-serif
}

.fonts-loaded body {
    font-family: "OpenSans", sans-serif
}

@media (min-width: 0) {
    .flexwrap body {
        max-width: inherit;
        margin: 0
    }
    @supports ( display: grid) and ( display: contents) {
        body {
            max-width: inherit;
            margin: 0;
        }
    }
}

* html body {
    width: 30em
}

.fonts-loaded h1,
.fonts-loaded h2,
.fonts-loaded h3,
.fonts-loaded h4,
.fonts-loaded h5,
.fonts-loaded h6,
.fonts-loaded strong {
    font-weight: 600
}

.a11y {
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    white-space: nowrap;
    width: 1px
}

.layout,
.catalog-table-layout {
    margin: 0 auto;
    max-width: 100em;
    width: 92%
}

.header-layout {
    margin: 0 auto;
    max-width: 100em;
    width: 92.5%
}

@media (min-width: 30em) {
    .layout,
    .header-layout {
        display: flex
    }
    @supports (display: grid) and (display: contents) {
        .layout {
            display: grid;
            grid-column-gap: 4%;
            grid-template-columns: repeat(2, 1fr)
        }
    }
}

.header-layout {
    justify-content: left;
}

@media (min-width: 50em) {
    .layout {
        grid-column-gap: 2%;
        grid-template-columns: repeat(3, 1fr)
    }
}

@media (min-width: 70em) {
    .layout {
        grid-column-gap: 2.3333333%;
        grid-template-columns: repeat(4, 1fr)
    }
}

@media (min-width: 90em) {
    .layout {
        grid-column-gap: 2.3333333%;
        grid-template-columns: repeat(5, 1fr)
    }
}

.hed--page {
    font-size: 1.4em;
    margin: 0;
    font-family: open_sans,"Helvetica Neue",Helvetica,Arial,sans-serif;
    font-weight: 300!important;
}

.hed--section {
    font-size: 0.75em;
    font-weight: 600;
    color: #657380;
    letter-spacing: 0.04em;
    margin-top: 0;
    margin-bottom: 0;
    text-transform: uppercase
}

.hed--section {
    font-family: Arial, Helvetica, sans-serif
}

.fonts-loaded .hed--section {
    font-family: "OpenSans", sans-serif
}

.hed--section {
    display: block;
}

.pg-title {
    padding: 1em 0;
    margin-bottom: .25em
}

.pg-title--inner {
    display: block
}

.blueprints-new,
.blueprints-new--list {
    align-items: center;
    display: flex;
}

@media (max-width: 50em) {
    .blueprints-new {
        margin: 1em 0
    }
}

@media (min-width: 40em) {
    .blueprints-new.collapsible-enhanced .collapsible-content {
        display: flex
    }
}

@media (max-width: 40em) {
    .blueprints-new--list {
        display: block;
        position: absolute
    }
}

.blueprints-new--list,
.blueprints-new--item {
    list-style: none;
    margin: 0;
    padding: 0
}

.blueprints-new--item {
    min-width: 8rem;
    max-width: 10rem;
}

.flexwrap .blueprints-new--item {
    margin: 0 0 0 0.5em
}

@media (max-width: 40em) {
    .blueprints-new.collapsible-enhanced {
        background-position: right 0.5em top 50%;
        background-color: #F8F8F8;
        border-radius: 0.3em;
        border: 1px solid #C3C3C3;
        font-size: 1.125em;
        font-weight: 600;
        margin: 0;
        padding: 0.3em;
        position: relative;
        transition: background-color 0.15s
    }
    .blueprints-new--title.collapsible-header {
        padding: 0.5em 1em 0.5em 0.2em
    }
    .blueprints-new--title.collapsible-header span {
        display: inline-block;
        text-indent: -1000em
    }
    .blueprints-new--list.collapsible-content {
        background-color: #F8F8F8;
        border: 1px solid #C3C3C3;
        border-radius: 0 0 0.3em 0.3em;
        border-width: 0 1px 1px;
        left: -1px;
        margin-top: -0.3em;
        top: 100%;
        width: 100%
    }
    .blueprints-new--list.collapsible-content .blueprints-new--item {
        display: block;
        margin: 0;
        padding: 0
    }
    .blueprints-new--list.collapsible-content .blueprints-new--btn {
        border-radius: 0;
        border: 0;
        display: block;
        font-size: 0.85em;
        font-weight: 400;
        padding-bottom: 0.65em;
        padding-top: 0.65em;
        background: #4099F1;
    }
}

@media (min-width: 40em) {
    .blueprints-new.collapsible.collapsible-enhanced {
        background-image: none
    }
    .pg-title--inner {
        align-items: flex-start;
        display: flex;
        justify-content: space-between;
        padding-left: 15px;
    }
}

.flexwrap .catalog-main {
    flex-direction: column
}

.tile--header {
    grid-column-start: 1;
    grid-column-end: 5
}

.catalog-actions {
    padding: 10px;
    margin-bottom: 0;
    margin-right: 15px;
}

.catalog-actions a {
    margin-left: 8px;
}

.catalog-actions a:after {
    content: '';
    display: inline-block;
    width: 1px;
    height: 9px;
    margin-left: 10px;
    background-color: #747474;
}

.catalog-actions a:first-child {
    margin-left: 0;
}

.catalog-actions a:last-child:after {
    display: none;
}

.catalog-filters,
.catalog-container,
.tile-list-controls,
.tile-list {
    width: 100%
}

.tile-list {
    display: contents;
    list-style: none;
    margin: 0;
    padding: 0
}

.tile-list:after {
    content: "";
    flex: auto;
    display: block
}

@supports (display: grid) and (display: contents) {
    .catalog-container {
        display: contents
    }
    .tile-list-controls,
    .tile-list {
        grid-column-start: 1;
        width: auto
    }
    .tile-list:after {
        content: none
    }
}

@media (min-width: 30em) {
    .tile-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between
    }
    @supports (display: grid) and (display: contents) {
        .tile-list {
            display: contents
        }
        .tile-list-controls,
        .tile-list {
            grid-column-end: span 2
        }
        .catalog-filters {
            min-width: 0
        }
    }
}

@media (min-width: 50em) {
    .flexwrap .catalog-main {
        flex-direction: row-reverse;
        flex-wrap: nowrap;
        justify-content: space-between
    }
    .catalog-container {
        flex: 2
    }
    @supports (display: grid) and (display: contents) {
        .catalog-filters,
        .catalog-container {
            max-width: inherit
        }
        .tile-list-controls,
        .tile-list {
            grid-column-end: span 2
        }
        .tile-list-controls.no-sidebar{
            grid-column-end: span 3
        }
    }
}

@media (min-width: 70em) {
    .catalog-container {
        flex: 3
    }
    @supports (display: grid) and (display: contents) {
        .tile-list-controls,
        .tile-list {
            grid-column-end: span 3
        }
        .tile-list-controls.no-sidebar{
            grid-column-end: span 4
        }
        .catalog-filters,
        .catalog-container {
            max-width: inherit
        }
    }
}

@media (min-width: 90em) {
    .catalog-container {
        flex: 4
    }
    @supports (display: grid) and (display: contents) {
        .tile-list-controls,
        .tile-list {
            grid-column-end: span 4
        }
        .tile-list-controls.no-sidebar{
            grid-column-end: span 5
        }
        .catalog-filters,
        .catalog-container {
            max-width: inherit
        }
    }
}

.tile-item {
    display: block;
    display: flex;
    min-height: 8em;
    margin-bottom: 1.5em;
    position: relative;
    transition: transform 0.15s ease-in-out;
}

@media (min-width: 30em) {
    .tile-item {
        min-height: 18.75em
    }
    .tile-item.tile--is-minimal {
        min-height: 16em
    }
    .tile-item:hover {
        transform: scale(1.05)
    }
    .flexwrap .tile-item,
    .tile-list:after {
        margin-bottom: 1.5em;
        flex: 1 0 48%;
        max-width: 48%
    }
    @supports (display: grid) and (display: contents) {
        .flexwrap .tile-item,
        .tile-list:after {
            max-width: inherit
        }
    }
}

@media (min-width: 70em) {
    .flexwrap .tile-item,
    .tile-list:after {
        flex: 1 0 32%;
        max-width: 32%
    }
    @supports (display: grid) and (display: contents) {
        .flexwrap .tile-item,
        .tile-list:after {
            max-width: inherit
        }
    }
}

@media (min-width: 90em) {
    .flexwrap .tile-item,
    .tile-list:after {
        flex: 1 0 24%;
        max-width: 24%
    }
    @supports (display: grid) and (display: contents) {
        .flexwrap .tile-item,
        .tile-list:after {
            max-width: inherit
        }
    }
}

.tile {
    background-color: #FFF;
    border-radius: 0 0 0.35em 0.35em;
    border-top: 3px solid #295179;
    display: block;
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
    padding: 1em 0 0;
    transition: box-shadow 0.15s ease-in-out;
}

.tile--is-featured .tile {
    background-position: -1000px -1000px
}

@media (min-width: 30em) {
    .tile--is-featured .tile {
        background-position: 100% 100%
    }
}

.tile:hover,
.tile:focus {
    text-decoration: none
}

@media (min-width: 30em) {
    .tile-item:hover .tile {
        box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25)
    }
}

.tile--logo {
    float: right;
    max-height: 75px;
    margin: 0 0 1em 1em;
    max-width: 75px
}

@media (min-width: 30em) {
    .tile--logo {
        float: none;
        display: block;
        margin: 0 auto 1em;
        max-height: 7em;
        max-width: 150px
    }
    .tile--logo-wrapper {
        min-height: 8em;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.tile--title {
    font-weight: 600;
    font-size: 1.125em;
    line-height: 1.33333333333333333333;
    margin: 0 0.75em;
    word-break: break-word;
}

@media (max-width: 30em) {
    .tile:hover .tile--title,
    .tile:focus .tile--title {
        text-decoration: underline
    }
}

.tile--footer {
    color: #737373;
    font-size: 0.875em;
    margin: 0;
    margin-top: auto;
    padding: 1em
}
@media (max-width: 29.9em) {
    .tile-list--header {
        display: flex;
        flex-direction: row-reverse;
        position: relative
    }
}

.tile-list--footer .layout .nav-page--footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    grid-column: 5;
}
.blueprints-new--btn {
    background: #4099F1;
    color: #fff;
    padding: .5rem;
}

@media (min-width: 50em) {
    .layout {
        grid-template-columns: repeat(3, 1fr);
    }
}
@media (min-width: 70em) {
    .layout {
        grid-template-columns: repeat(4, 1fr);
    }
}

@media (min-width: 90em) {
    .layout {
        grid-template-columns: repeat(5, 1fr);
    }
}
